* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  background-color: hsla(271, 76%, 70%, 0.697);

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}

.time_style {
  font-family: poppins;
  font-weight: bold;
  font-size: 4rem;
  letter-spacing: 2px;
}

.center_style {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

button {
  height: 60px;
  width: 200px;
  border: 2px solid aliceblue;
  color: white;
  font-size: 1.5rem;
  font-weight: bold;
  background-color: hsl(271, 76%, 53%, 0.75);
  border-radius: 5px;
  cursor: pointer;
  margin-top: 20px;
}
